* {
    box-sizing: border-box;

}

main {
    margin: auto;
    background-color: white;
    height: 600px;
    max-width: 800px;
    border-radius: 5px;
    padding: 50px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: center;
}

.content-box {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}

.title {
    font-family: 'Poppins', sans-serif;
    font-size: 40px;
    margin: 0;
}

.instructions {
    font-family: 'Poppins', sans-serif;
    font-weight: 300;
    margin-top: 0;
    text-align: center;
    font-size: 25px;
}

.rolls-performed {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    justify-content: center;
    text-align: center;
    font-size: 25px;
}

.dice-box {
    display: grid;
    grid-template: auto auto / repeat(5, 1fr);
    gap: 20px;
    margin-bottom: 40px;
}

.die-front {
    height: 100px;
    width: 100px;
    box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
    border-radius: 10px !important;
    justify-content: center;
    align-items: center;
    display: grid;
    grid-template-areas:
        "a . c"
        "e g f"
        "d . b";

    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: repeat(3, 1fr);
    padding: .5rem;
}

.bg-die-front {
    border-radius: 10px;
}

.dot {
    align-self: center;
    background-color: black;
    border-radius: 50%;
    box-shadow: inset -0.15rem 0.15rem 0.25rem rgba(0, 0, 0, 0.5);
    display: block;
    height: 1.2rem;
    margin: 4px;
    justify-self: center;
    width: 1.2rem;
}

.dot:nth-child(odd):last-child {
    grid-area: g;
}

.dot:nth-child(2) {
    grid-area: b;
}

.dot:nth-child(3) {
    grid-area: c;
}

.dot:nth-child(4) {
    grid-area: d;
}

.dot:nth-child(5) {
    grid-area: e;
}

.dot:nth-child(6) {
    grid-area: f;
}

.dice-button {
    padding: 8px;
    margin-bottom: 10px;
    height: 75px;
    width: 175px;
    border: none;
    border-radius: 5px;
    background-color: #457b9d;
    color: white;
    font-size: 1.5rem;
    font-family: 'Poppins', sans-serif;
    font-weight: 600;
    cursor: pointer;
}

.dice-button:focus {
    outline: none;
}

.dice-button:active {
    box-shadow: inset 5px 5px 10px -3px rgba(0, 0, 0, 0.7);
}


html {
    padding: 20px;
    height: 100%;
    background: radial-gradient(ellipse at bottom, #1b2735 0%, #090a0f 100%);
    overflow: hidden;
}

@media only screen and (max-width: 821px) {

    html,
    body {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        overflow-x: hidden;
    }

    .content-box {
        padding-bottom: 25px;
    }

    main {
        background-color: white;
        height: 650px;
        max-width: 620px;
        border-radius: 5px;
        padding: 50px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

    }
}

@media only screen and (max-width: 450px) {

    html,
    body {
        width: 100%;
        height: 100%;
        margin: 0;
        padding: 0;
        overflow-x: hidden;
        overflow-y: hidden;
    }

    .content-box {
        max-height: 100%;
        max-width: 100%;
    }

    .main-box {
        height: 850px !important;
        max-width: 100%;
        width: 350px;
    }

    .instructions {
        font-family: 'Poppins', sans-serif;
        font-weight: 300;
        margin-top: 0;
        text-align: center;
        font-size: 15px;
    }

    .title {
        font-family: 'Poppins', sans-serif;
        font-size: 25px;
        margin: 0;
    }

    .rolls-performed {
        font-family: 'Poppins', sans-serif;
        font-weight: 700;
        justify-content: center;
        text-align: center;
        font-size: 25px;
        margin: 0;
    }

    main {
        background-color: white;
        height: 750px;
        max-width: 350px;
        border-radius: 5px;
        display: flex;
        flex-direction: column;
        justify-content: space-around;
        align-items: center;

    }

    .dice-box {
        display: grid;
        grid-template: auto auto / repeat(2, 1fr);
        gap: 5px 50px;
        margin-bottom: 20px;
    }

    .die-front {
        height: 90px;
        width: 90px;
        box-shadow: 0px 2px 2px rgba(0, 0, 0, 0.15);
        border-radius: 10px !important;
        justify-content: center;
        align-items: center;
        display: grid;
        grid-template-areas:
            "a . c"
            "e g f"
            "d . b";

        grid-template-columns: repeat(3, 1fr);
        grid-template-rows: repeat(3, 1fr);
        padding: .5rem;
    }

    .dot {
        align-self: center;
        background-color: black;
        border-radius: 50%;
        box-shadow: inset -0.15rem 0.15rem 0.25rem rgba(0, 0, 0, 0.5);
        display: block;
        height: 1.2rem;
        margin: 4px;
        justify-self: center;
        width: 1.2rem;
    }

    .dice-button {
        padding: 10px;
        margin-top: 20px;
        height: 75px;
        width: 175px;
        border: none;
        border-radius: 5px;
        background-color: #457b9d;
        color: white;
        font-size: 1.5rem;
        font-family: 'Poppins', sans-serif;
        font-weight: 600;
        cursor: pointer;
    }

}


